// import { debounce } from "debounce";
var _ = require('lodash');

class Device3dPerspective extends React.Component {

  constructor(props) {
    super(props);
    
    // this.respondToPosition = _.throttle(this.respondToPosition.bind(this), 50); // 50
    
    // this._throttledMouseMove = _.throttle(this._throttledMouseMove.bind(this), 1000);

  }

  private roundDown = function (value, roundTo) {
    return Math.floor(value / roundTo) * roundTo;
  }

  private roundUp = function (value, roundTo) {
    return Math.ceil(value / roundTo) * roundTo;
  }

  // _throttledMouseMove = (e) => {
  //   console.log(e.screenX);
  // }

  private _onMouseMove = (e) => {
    e.persist();
    // this.respondToPosition(e);
    this.respondToPositionThrottled(e);
    // this._throttledMouseMove(e);
  }

  

  private respondToPosition = (event: MouseEvent) => {

    // console.log('event', event);

    // event.persist();
    // if (!event || !event.pageX || !event.pageY) {
    //   console.log('return');
    //   return;
    // }

    const deviceElement = document.querySelector('.device-3d') as HTMLElement;
    if (!deviceElement) {
      return;
    }
    
    // Get position of element, relative to window
    const elementX = deviceElement.getBoundingClientRect().left;
    const elementY = deviceElement.getBoundingClientRect().top;

    // Get center of element, relative to itself
    const centerX = parseInt((deviceElement.offsetWidth / 2).toString(), 10);
    const centerY = parseInt((deviceElement.offsetHeight / 2).toString(), 10);

    // Get mouse position, relative to window
    const positionX = event.pageX;
    const positionY = event.pageY;
  
    // Get mouse position, relative to element
    const distanceX = parseInt((positionX - (elementX + centerX)).toString(), 10);
    let distanceY = parseInt((positionY - (elementY + centerY)).toString(), 10);
    // Invert transformY (200 becomes -200, -400 becomes 400)
    if (distanceY > 0) {
      distanceY = 0 - distanceY;
    } else if (distanceY < 0) {
      distanceY = 0 - (distanceY + 2);
    }
    
    // console.log(`elementX: ${elementX}, elementY: ${elementY}`);
    // console.log(`centerX: ${centerX}, centerY: ${centerY}`);
    // console.log(`positionX: ${positionX}, positionY: ${positionY}`);
    // console.log(`distanceX: ${distanceX}, distanceY: ${distanceY}`);

    // Normalise, and use distaceX for rotating on Y, and distanceY for rotating on X
    const roundTo = 2;
    const reducerX = 40;
    const reducerY = 40;
    let rotateX = 0;
    let rotateY = 0;
    let style = '';
    if (distanceX !== null) {
      // Reduce
      rotateY = parseInt((distanceX / reducerX).toString(), 10);
      // Round up/down
      rotateY = rotateY > 0 ? this.roundDown(rotateY, roundTo) : this.roundUp(rotateY, roundTo);
      style += ` rotateY( ${rotateY}deg )`;
    }
    if (distanceY !== null) {
      // Reduce
      rotateX = parseInt((distanceY / reducerY).toString(), 10);
      // Round up/down
      rotateX = rotateX > 0 ? this.roundDown(rotateX, roundTo) : this.roundUp(rotateX, roundTo);
      style += ` rotateX( ${rotateX}deg )`;
    }

    // console.log(`style: ${style}`);
   
    if (style) {
      deviceElement.setAttribute('style', `transform: ${style}`);
    // } else {
      // deviceElement.removeAttribute('style');
    }

  }

  private respondToPositionThrottled = _.throttle(this.respondToPosition, 20); // 50
   
  // private animate3dIn = (className: string) => {
  //   console.log('animate3dIn!');
  //   const deviceElement = document.querySelector('.device-3d');
  //   if (deviceElement) {
  //     deviceElement.classList.contains(className) ?
  //       deviceElement.classList.remove(className) : deviceElement.classList.add(className);
  //   }
  // }

  // private animate3dOut = () => {
  //   console.log('animate3dOut!');
  //   const deviceElement = document.querySelector('.device-3d');
  //   if (deviceElement) {
  //     deviceElement.classList.remove('tilt-lt');
  //   }
  // }

  render() {
    return (
      <div className="device-perspective"
        onMouseMove={this._onMouseMove}>
        {/* onPointerMove={(event) => respondToPosition(event)}> */}
        {/* onPointerMove={debounce(respondToPosition, 250)}> */}

        {/* <div className="debug-position" >(Position)</div> */}

        <div className="device-3d">


          {/* <div className="button-corner button-corner-tl"
            onMouseDown={() => this.animate3dIn('tilt-lt')}
            onMouseUp={() => this.animate3dOut()}
            onMouseOut={() => this.animate3dOut()}
            onClick={() => this.animate3dOut()}
          ></div> */}
          <div className="device-layer device-layer-content-tasks"></div>
          <div className="device-layer device-layer-content-project"></div>
          <div className="device-layer device-layer-content-header"></div>
          <div className="device-layer device-layer-iphone"></div>
          <div className="device-layer device-layer-screen-photo-fg"></div>
          <div className="device-layer device-layer-screen-photo-bg"></div>

        </div>
      </div>
    )
  }
}


export default Device3dPerspective
